import React from 'react';
import jss from 'jss';
import preset from 'jss-preset-default';
import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';

import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";


import logo from '../images/logos/superdrug.png';
import primaryImage from '../images/details-images/primary/superdrug_dev.png';
// import reactLogo from '../images/tools/React-icon.svg';
import {html, css, js, bootstrap} from '../config';

jss.setup(preset());

const styles = {
    grad: {

        backgroundImage: 'linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%)'
    }
}

const {classes} = jss.createStyleSheet(styles).attach();


export const SuperdrugDev = (props) => {
    const settings = {
        className: "center",
        centerMode: true,
        centerPadding: "60px",
        speed: 500,
        dots: true,
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        adaptiveHeight: true,
        lazyLoad: true,
        dotsClass: "slick-dots slick-thumb"
    };

    const sliderImagesPath = 'images/slider/superdrug_dev/';

    const sliderImages = [
        'desktop2.jpg',
        'desktop1.jpg',
        'desktop3.jpg',
        'mobile2.jpg',
        'mobile1.jpg',
        'mobile3.jpg'
    ];

    const toolsList = [
        {caption:"HTML", image: html, supportingCap:null},
        {caption:"CSS", image: css, supportingCap:null},
        {caption:"Javascript", image: js, supportingCap:null},
        {caption:"Bootstrap", image: bootstrap, supportingCap:null}
    ]

    return (
        <React.Fragment>
            <Container>
                <Row>
                    <Col xs="12">
                        <div className="logo"><img alt="Mastercard" src={logo}/></div>
                        <h1 className="title">Superdrug Plc</h1>
                        <h3>A popular high-street health and beauty retailer's online e-commerce presence.
                            <br/>The goal was to improve on the online experience for the main retail website to compete with the top market players and increase sales.
                        </h3>
                        
                        <ul className="sectors">
                            { props.industry ? 
                                props.industry.map((sector, i) => <li key={i}>{sector}</li>) : null
                            }
                        </ul>

                    </Col>
                </Row>
            </Container>
                    
            <div className={`grad ${classes.grad}`}>
                <Container className="textCenter">
                    <img className="primary" alt="Superdrug Plc" src={primaryImage}/>
                </Container>
            </div>

            <Container>
                <Row>
                    <Col sm="5" lg="4">
                        <h2>Tools Used</h2>
                        <ul className="tools-list noBullet noMargin noPad marBottomTwice">
                            { toolsList.map(item => {
                                return (
                                    <li key={item.caption}>
                                        <div className="mar10Right"><img alt={item.caption} src={item.image}/></div>  
                                        <div>{item.caption} {item.supportingCap?<span className="sizeNormal">{item.supportingCap}</span>:null}</div>
                                    </li>
                                )
                            }) }
                            
                        </ul>
                    </Col>
                    <Col sm="7" lg="8">
                        <h2>Activities</h2>
                        <ul className="custom">
                            <li>Worked as the Lead Front-end Dev on the program.</li>
                            <li>Following the design mockups and implement pixel perfect UI on the front-end with modern UI code techniques.</li>
                            <li>Curated mobile site to complement well crafted desktop site.</li>
                            <li>Worked closely with the back-end team and provide optimized front-end support for the back-end features.</li>
                        </ul>
                    </Col>
                    <Col xs="12">
                        <h2 className="noMargin marTopTwice">Screens</h2> <p className="sizeSmall">(Please click on the slide image to view the full version of the image)</p>
                       
                        <div className={`slider-wrapper ${classes.grad}`}>
                            <Slider {...settings}>
                                {sliderImages.map(item => {
                                    return (
                                        <div key={item} className="slide">
                                            <a 
                                                href={`${sliderImagesPath}${item}`} 
                                                target="_blank" 
                                                rel="noopener noreferrer">
                                                <img 
                                                    src={`${sliderImagesPath}${item}`} 
                                                    alt='Superdrug Plc' 
                                                />
                                            </a>
                                        </div>
                                    )
                                })}
                            </Slider>
                        </div>

                    </Col>
                </Row>
            </Container>
                    
        </React.Fragment>
    )
}