import React from 'react';
import jss from 'jss';
import preset from 'jss-preset-default';
import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';

import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";


import logo from '../images/logos/natwest.png';
import primaryImage from '../images/details-images/primary/natwest_ui.png';
// import reactLogo from '../images/tools/React-icon.svg';
import {sketch, illustrator, photoshop, zeplin, invision} from '../config';

jss.setup(preset());

const styles = {
    grad: {

        backgroundImage: 'linear-gradient(to top, #3b41c5 0%, #a981bb 49%, #ffc8a9 100%)'

    }
}

const {classes} = jss.createStyleSheet(styles).attach();


export const NatwestUi = (props) => {
    const settings = {
        className: "center",
        centerMode: true,
        centerPadding: "60px",
        speed: 500,
        dots: true,
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        adaptiveHeight: true,
        lazyLoad: true,
        dotsClass: "slick-dots slick-thumb"
    };

    const sliderImagesPath = 'images/slider/natwest_ui/';

    const sliderImages = [
        'image3.png',
        'image1.png',
        'image2.png',
        'image4.png',
        'image5.png',
        'image6.png',
        'image7.png',
        'image8.png'
    ];

    const toolsList = [
        {caption:"Sketch", image: sketch, supportingCap:null},
        {caption:"Illustrator", image: illustrator, supportingCap:null},
        {caption:"Photoshop", image: photoshop, supportingCap:null},
        {caption:"Zeplin", image: zeplin, supportingCap:null},
        {caption:"Invision", image: invision, supportingCap:null}
    ]

    return (
        <React.Fragment>
            <Container>
                <Row>
                    <Col xs="12">
                        <div className="logo"><img alt="Mastercard" src={logo}/></div>
                        <h1 className="title">Natwest Premier App</h1>
                        <h3>A mobile app for Natwest Premier account holders which offers exclusive benefits and bookings system. Goal was to develop an Elite class app to target the high end Natwest premier account holders. <br />
                            The app's main purpose is to give NatWest Premier customers access to different services, such us buying cinema e-codes, booking holidays and event tickets within a seamless experience.
                        </h3>
                        
                        <ul className="sectors">
                            { props.industry ? 
                                props.industry.map((sector, i) => <li key={i}>{sector}</li>) : null
                            }
                        </ul>

                    </Col>
                </Row>
            </Container>
                    
            <div className={`grad ${classes.grad}`}>
                <Container className="textCenter">
                    <img className="primary" alt="Natwest Premier App" src={primaryImage}/>
                </Container>
            </div>

            <Container>
                <Row>
                    <Col sm="12">
                        <h2>The Challenge</h2>
                        <p>To provide Natwest Premier account holders a scalable and flexible platform which provides with a comprehensive loyalty program where the customers can benefit from a number of offers, vouchers codes and travel booking discounts. The interface needed to be intuitive and target audience belonged to the high end market, so a very carefully researched, seamless and user centered approach was required.</p>
                    </Col>
                </Row>
                <Row>
                    <Col sm="4" lg="3">
                        <h2>Tools Used</h2>
                        <ul className="tools-list noBullet noMargin noPad marBottomTwice">
                            { toolsList.map(item => {
                                return (
                                    <li key={item.caption}>
                                        <div className="mar10Right"><img alt={item.caption} src={item.image}/></div>  
                                        <div>{item.caption} {item.supportingCap?<span className="sizeNormal">{item.supportingCap}</span>:null}</div>
                                    </li>
                                )
                            }) }
                            
                        </ul>
                    </Col>
                    <Col sm="8" lg="9">
                        <h2>Activities Summary</h2>
                        <ul className="custom">
                            <li>Visual research, brainstorming, developing useful ideas.</li>
                            <li>Wireframing and User journeys.</li>
                            <li>Working closely with marketing and product team, software engineers and project management to devise an interface appealing to the high end market.</li>
                            <li>Utilization of the client branding and visual language to ensure usability and effectiveness of the product.</li>
                            <li>UI design and behaviour, integration of contents with the marketing team.</li>
                            <li>Developing the brand identity and visual language.</li>
                            <li>Mentoring Junior designers.</li>
                        </ul>
                    </Col>
                </Row>
                <Row>
                <Col sm="12">
                        <h2>Strategy, Planning and Information Gathering</h2>
                        <p>Kick-off meeting with stakeholders was the first step for me to start gathering key information about the project. After that discovery phase starts, a high-intensity effort that allowed me to define project milestones, review the competitor landscape, understand Natwest premier user vision, and begin research into user needs, behaviour and pain-points.</p>
                    </Col>
                    <Col sm="12" className="floating-img-container">
                        <img src="images/casestudies/natwestPremier_TeamGantt.png" alt="Team Gantt Project Planning"/>
                    </Col>
                </Row>
                <Row>
                    <Col sm="12">
                        <h2>Agile approach</h2>
                        <p>I laid out an Agile UX plan for the project as shown below, enabling UX and design process to fully align with development. This example is based on 3 features, but we had a lot more than that, we kept on going using this approach.</p>
                    </Col>
                    <Col sm="12" className="floating-img-container resetHeight">
                        <img className="pad10" src="images/casestudies/natwestPremier_agile.png" alt="Agile Planning"/>
                    </Col>
                </Row>
                <Row>
                    <Col sm="6">
                        <h2>User Journeys</h2>
                        <p>Created personas based on which I created an user journey which satisfies most of the needs of the user personas. This journey was testing with multiple users and apparently worked with the majority.</p>
                        <h2>Prototyping/Wireframing</h2>
                        <p>Started with the low fidelity prototyping. I used sketch for static prototyping which was based on the drawings and paper sketches from the stakeholder meetings. I had everything clear in my mind hence decided go through the wire-framing stage to translate the ideas into a visual and functional structure of the app interface.</p>
                    </Col>
                    <Col sm="6" className="floating-img-container noShadow">
                        <img className="" src="images/casestudies/natwestPremier_wireframes.png" alt="Natwest Premier app Wireframes"/>
                    </Col>
                </Row>
                <Row>
                    <Col sm="8" className="floating-img-container noShadow">
                        <img className="    " src="images/casestudies/natwestPremier_styling.png" alt="Natwest Premier App Style Guide"/>
                    </Col>
                    <Col sm="4">
                        <h2>Style Guidelines</h2>
                        <p>To help developers and external partners working on the project, I created style guidelines based on Natwest Premier brand guidelines. That helped greatly to keep everything consistent throughout the app's digital experience.</p>
                    </Col>
                </Row>
                <Row>
                    <Col xs="12">
                    <h2>High Fidelity UI Design</h2>
                    <p>After the sign off on the wireframes and the style guide, I was ready to move on to UI bit. I used sketch to prepare detailed UI Mockups covering all user journeys and made it available on Zeplin for the developers to pickup the assets and code. I constantly was engaged in meetings with developers to ensure that UI is not proposing something which can pose challenges to developers and delay the launch.</p>
                    </Col>
                </Row>
                
                <Row>
                    <Col xs="12">
                        <h2 className="noMargin marTopTwice">Screens</h2> <p className="sizeSmall">(Please click on the slide image to view the full version of the image)</p>
                       
                        <div className={`slider-wrapper ${classes.grad}`}>
                            <Slider {...settings}>
                                {sliderImages.map(item => {
                                    return (
                                        <div key={item} className="slide">
                                            <a 
                                                href={`${sliderImagesPath}${item}`} 
                                                target="_blank" 
                                                rel="noopener noreferrer">
                                                <img 
                                                    className="noShadow"
                                                    src={`${sliderImagesPath}${item}`} 
                                                    alt='Natwest Premier App' 
                                                />
                                            </a>
                                        </div>
                                    )
                                })}
                            </Slider>
                        </div>

                    </Col>
                </Row>
                
            </Container>
                    
        </React.Fragment>
    )
}