import React from 'react';
import jss from 'jss';
import preset from 'jss-preset-default';
import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';

import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";


import logo from '../images/logos/rbs.png';
import primaryImage from '../images/details-images/primary/rbs_ui.png';
import {axure, illustrator, photoshop, invision, penpaper, whiteboard} from '../config';

jss.setup(preset());

const styles = {
    grad: {
        backgroundImage: 'linear-gradient(-20deg, #b721ff 0%, #21d4fd 100%)'
    }
}

const {classes} = jss.createStyleSheet(styles).attach();


export const RbsUi = (props) => {
    const settings = {
        className: "center",
        centerMode: true,
        centerPadding: "60px",
        speed: 500,
        dots: true,
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        adaptiveHeight: true,
        lazyLoad: true,
        dotsClass: "slick-dots slick-thumb"
    };

    const sliderImagesPath = 'images/slider/rbs_ui/';

    const sliderImages = [
        'styling.jpg',
        'desktop2.jpg',
        'desktop1.jpg',
        'desktop3.jpg',
        'mobile1.jpg',
        'mobile2.jpg',
        'mobile3.jpg',
        'mobile4.jpg'
    ];

    const toolsList = [
        {caption:"Axure RP", image: axure, supportingCap:null},
        {caption:"Illustrator", image: illustrator, supportingCap:null},
        {caption:"Photoshop", image: photoshop, supportingCap:null},
        {caption:"invision", image: invision, supportingCap:null},
        {caption:"Pen & Paper", image: penpaper, supportingCap:null},
        {caption:"whiteboard", image: whiteboard, supportingCap:null}
    ]

    return (
        <React.Fragment>
            <Container>
                <Row>
                    <Col xs="12">
                        <div className="logo"><img alt="Mastercard" src={logo}/></div>
                        <h1 className="title">RBS Travel</h1>
                        <h3>A travel related booking website for RBS customers giving access to some exclusive travel offers.<br/>Travel website is one of the most lucrative projects among other RBS ventures I was involved in and begged a modern corporate design on top of already well defined RBS styling guidelines.</h3>
                        
                        <ul className="sectors">
                            { props.industry ? 
                                props.industry.map((sector, i) => <li key={i}>{sector}</li>) : null
                            }
                        </ul>

                    </Col>
                </Row>
            </Container>
                    
            <div className={`grad ${classes.grad}`}>
                <Container className="textCenter">
                    <img className="primary" alt="RBS Travel" src={primaryImage}/>
                </Container>
            </div>

            <Container>
                <Row>
                    <Col sm="12">
                        <h2>The Challenge</h2>
                        <p>RBS had an existing Travel product which enabled its customers to do travel booking, the concern RBS presented was that the existing website was outdated and desired an approach built upon optimized user experience solutions as well having a modern sleek and appealing visual design. There were a few platform limitations, challenge was to deliver the best possible user experience solution around the limitations as well as clean and uncluttered interface.</p>
                    </Col>
                </Row>
                <Row>
                    <Col sm="5" lg="4">
                        <h2>Tools Used</h2>
                        <ul className="tools-list noBullet noMargin noPad marBottomTwice">
                            { toolsList.map(item => {
                                return (
                                    <li key={item.caption}>
                                        <div className="mar10Right"><img alt={item.caption} src={item.image}/></div>  
                                        <div>{item.caption} {item.supportingCap?<span className="sizeNormal">{item.supportingCap}</span>:null}</div>
                                    </li>
                                )
                            }) }
                            
                        </ul>
                    </Col>
                    <Col sm="7" lg="8">
                        <h2>Activities Summary</h2>
                        <ul className="custom">
                            <li>Visual research, brainstorming, developing useful ideas.</li>
                            <li>Working closely with marketing, software engineers and project management to devise the UX plan and ultimately create a modern corporate interface.</li>
                            <li>UI design and behaviour, integration of contents with the marketing team.</li>
                            <li>Conversion of wire-frame prototypes into visual interface.</li>
                            <li>Developing the brand identity and visual language.</li>
                            <li>Built a generic design language, laid out rules forming a style guide.</li>
                            <li>Formed a design which is easy to be themed as per the nature of the project.</li>
                            <li>Adaptive design which fits all screen across all platforms.</li>
                        </ul>
                    </Col>
                </Row>
                <Row>
                    <Col sm="12">
                        <h2>Strategy, Planning and Information Gathering</h2>
                        <p>My first step with this project was to plan the UX/UI strategy. The client knew what they wanted, but wanted to see strategy planned on how to achieve the goal. I arranged  interviews with stakeholders, engineering and development teams and PMs to understand everyone's point of view and the end goal. After the interviews I put the requirements on the whiteboard and started connecting dots, I then constructed task breakdown and timeline up using teamgantt and made it available to all stakeholders who could collaborate with the task breakdown and identifying milestones on the timeline.</p>
                    </Col>
                    <Col sm="12" className="floating-img-container">
                        <img src="images/casestudies/rbsTravel_board.png" alt="RBS Travel Strategy"/>
                    </Col>
                </Row>
                <Row>
                    <Col sm="6">
                        <h2>Information Architecture</h2>
                        <p>After completing initial research and user information gathering and creating
                        foundations for the whole project. My next step was to create an information architecture. I used card sorting exercise to get this done. The part of information architecture was to create a flawless navigation allowing users to not only use the product but easily navigate to the whole travel portal.</p>
                        <h2>Product Flow</h2>
                        <p>I created not only step by step journeys but also recorded any possible pain points user can face during these steps and recommendations how to tackle them.</p>
                    </Col>
                    <Col sm="6" className="floating-img-container noShadow">
                        <img className="" src="images/casestudies/rbsTravel_flow.png" alt="Natwest Premier app Wireframes"/>
                    </Col>
                </Row>
                <Row>
                    <Col sm="8" className="floating-img-container noShadow">
                        <img className="    " src="images/casestudies/rbsTravel_wireframe.png" alt="Natwest Premier App Style Guide"/>
                    </Col>
                    <Col sm="4">
                        <h2>Prototyping / Interaction Design</h2>
                        <p>Once all the required information was present, I started with fully functional prototype. I believe prototype is the UX segment which makes or breaks the whole project. Prototype should always be detailed, showing as many interactions as possible, so it can be tested on users and developers don’t have to ask thousands of questions.</p>
                    </Col>
                </Row>
                <Row>
                    <Col xs="12">
                    <h2>High Fidelity UI Design</h2>
                    <p>The last step of the project was to create a UI which can translated the UX design perfectly. I created the design system laying our rules of the design language using existing RBS style guide. I created UI design for desktop and mobile. Prototype was there to follow the interactions and design was there to tell devs everything else.</p>
                    </Col>
                </Row>
                <Row>
                    <Col xs="12">
                        <h2 className="noMargin marTopTwice">Screens</h2> <p className="sizeSmall">(Please click on the slide image to view the full version of the image)</p>
                    
                        <div className={`slider-wrapper ${classes.grad}`}>
                            <Slider {...settings}>
                                {sliderImages.map(item => {
                                    return (
                                        <div key={item} className="slide">
                                            <a 
                                                href={`${sliderImagesPath}${item}`} 
                                                target="_blank" 
                                                rel="noopener noreferrer">
                                                <img 
                                                    src={`${sliderImagesPath}${item}`} 
                                                    alt='RBS Travel' 
                                                />
                                            </a>
                                        </div>
                                    )
                                })}
                            </Slider>
                        </div>

                    </Col>
                </Row>
                
            </Container>
                    
        </React.Fragment>
    )
}